import Switch from '../components/Switch';
import * as actions from '../actions/';
import { connect, Dispatch } from 'react-redux';
import { IStoreState } from '../types';

const mapStateToProps = (state: IStoreState) => {
    return {
        label: state.growWhenEatReducer.label,
        value: state.growWhenEatReducer.value
    };
};

const mapDispatchToProps = (dispatch: Dispatch<actions.IToggleGrowWhenEat>) => ({
    onToggle: () => dispatch(actions.toggleGrowWhenEat())
});

const GrowWhenEating = connect(mapStateToProps, mapDispatchToProps)(Switch);
export default GrowWhenEating;


