import Switch from '../components/Switch';
import * as actions from '../actions/';
import { connect, Dispatch } from 'react-redux';
import { IStoreState } from '../types';

const mapStateToProps = (state: IStoreState) => {
    return {
        label: state.eatSelfReducer.label,
        value: state.eatSelfReducer.value
    };
};

const mapDispatchToProps = (dispatch: Dispatch<actions.IToggleEatSelf>) => ({
    onToggle: () => dispatch(actions.toggleEatSelf())
});

const EatSelfSwitch = connect(mapStateToProps, mapDispatchToProps)(Switch);
export default EatSelfSwitch;


