import * as constants from '../constants';

export interface IToggleHitWall {
    type: constants.TOGGLE_DIES_WHEN_HIT_WALL;
}

export const toggleHitWall = (): IToggleHitWall => ({
    type: constants.TOGGLE_DIES_WHEN_HIT_WALL
});

export interface IToggleEatSelf {
    type: constants.TOGGLE_DIES_WHEN_EAT_SELF;
}

export const toggleEatSelf = (): IToggleEatSelf => ({
    type: constants.TOGGLE_DIES_WHEN_EAT_SELF
});

export interface IToggleGrowWhenEat {
    type: constants.TOGGLE_GROWS_WHEN_EAT_FOOD;
}

export const toggleGrowWhenEat = (): IToggleGrowWhenEat => ({
    type: constants.TOGGLE_GROWS_WHEN_EAT_FOOD
});

export interface IToggleHighSpeed {
    type: constants.TOGGLE_HIGH_SPEED;
}

export const toggleHighSpeed = (): IToggleHighSpeed => ({
    type: constants.TOGGLE_HIGH_SPEED
});


