import React, { Component } from 'react';
import '../../styles/field.css';

export interface IFieldProps {
  name: string;
  label?: string;
  placeholder?: string;
  type?: string;
  description?: string;
  value: string | number;
  disabled?: boolean;
  required: boolean;
  minLength?: number;
  maxLength?: number;
  min?: number;
  max?: number;
  step?: number;
  size?: number;
  onChangeEvent?: (v: any) => void;
}

interface IFieldState {
  name: string;
  value: string | number;
  focus: boolean;
}

class Field extends Component<IFieldProps, IFieldState> {
  constructor(props: IFieldProps) {
    super(props);
    this.state = {
      name: props.name,
      value: props.value || '',
      focus: false
    };
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(e: any): void {
    var value = e.target.value;
    this.setState({
      value: value,
    });
  }

  render() {
    return (
      <div className="field">
        <div>
          <span className={'field-label ' + (this.state.value || this.props.label ? '' : 'field-label-hidden')}>
            {(this.props.label || this.props.placeholder || this.props.name) + (this.props.required ? ' *' : '')}
          </span>
        </div>
        <input
              className={'input ' + (' input-' + this.props.size)}
              type={this.props.type || 'text'}
              name={this.props.name}
              value={this.state.value}
              onChange={this.handleChange}
              placeholder={(this.props.placeholder || this.props.name)
                + (this.props.required && !this.props.label ? ' *' : '')}
              disabled={this.props.disabled}
              required={this.props.required}
              minLength={this.props.minLength}
              min={this.props.min}
              max={this.props.max}
              step={this.props.step}
        />
          <div>
            <div className="field-description">
              <span>{this.props.description}</span>
            </div>
          </div>
      </div>
    );
  }
}
export default Field;



